<template>
  <div class="list">
    <h2>車輛管理</h2>
    <div class="seach">
      <el-col :span="4">
        <el-input placeholder="輸入關鍵字~~~" prefix-icon="el-icon-search"></el-input>
      </el-col>
	    <el-col :span="4">
	      <el-date-picker type="date" placeholder="選擇日期"></el-date-picker>
	    </el-col>
	    <button>OK</button>
    </div>
    <router-link v-for="(item,index) in list.data" :to="'/CarMore/'+item.Id">
      <div class="list-d">{{item.CarNo}}</div>
	</router-link>
	<div class="no-found" v-if="list.data&&list.data.length<1">查無資料</div>
	<Page :max="list.pageCount" :url="/CarList/"/>
  </div>
</template>

<script>
import Seach from './Seach'
import Page from './Page'
export default {
  data() {
    return {
	  list:[],
	  show_seach:{
	    sel:true
	  }
	}
  },
  components: {Page,Seach},
  mounted:async function() {
    this.GLOBAL.show_black=true
	await this.all_ready()
    await this.get_data(this.$route.params.page)
	this.GLOBAL.show_black=false
  },
  watch:{
    async $route(to, from){
	  this.GLOBAL.show_black=true
      await this.get_data(this.$route.params.page)
	  this.GLOBAL.show_black=false
    }
  },
  methods:{
    get_data:async function(x) {
	  this.list=await this.Function.get_car(x)
	  console.log(this.list)
	}
  }
}
</script>




